@import "https://fonts.googleapis.com/css?family=Lato:400,400italic|Montserrat:400,700";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

html, body {
  height: 100%;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section {
  display: block;
}

ol, ul {
  list-style: none;
}

a {
  text-decoration: none;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  font-family: var(--body-font, -apple-system, BlinkMacSystemFont, "Segoe UI", "Noto Sans", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji");
  color: #403e3f;
  opacity: 1;
  background-color: #fff;
  background-repeat: no-repeat;
  background-attachment: fixed;
  margin-top: 0;
  font-size: 14px;
  line-height: 1.6em;
}

main {
  margin-bottom: 80px;
}

.meta {
  margin-top: 40px;
  padding: 0;
}

.meta p {
  margin-bottom: 20px;
  font-size: 14px;
}

a {
  color: #171717;
}

nav {
  max-width: 800px;
  border-bottom: 1px solid #000;
  margin-bottom: 25px;
  margin-left: 40px;
  margin-right: 40px;
  padding: 40px 0 25px;
}

nav h1, nav h2 {
  font-family: var(--header-font, -apple-system, BlinkMacSystemFont, "Segoe UI", "Noto Sans", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji");
  text-transform: uppercase;
  font-size: 44px;
  line-height: 1.2em;
}

nav h2 {
  font-size: 25px;
  font-weight: normal;
}

nav ul {
  margin-top: 10px;
}

nav ul li {
  padding-right: 10px;
  display: inline;
}

nav a {
  color: #403e3f;
}

section {
  max-width: 800px;
  margin-left: 40px;
  margin-right: 40px;
  font-weight: normal;
}

section a {
  color: #171717;
  border-bottom: 1px dotted #000;
}

section a:hover {
  color: #fff;
  background-color: #171717;
}

section p, section ul, section ol {
  margin-bottom: 15px;
  font-size: 16px;
  font-weight: 300;
}

section h2 {
  text-transform: uppercase;
  margin: 40px 0 20px;
  font-weight: normal;
}

section h2:first-child {
  margin-top: 20px;
}

body.index .intro p {
  font-size: 16px;
}

body.index section h1 {
  margin: 30px 0 10px;
  font-size: 22px;
}

body.index section h2 {
  color: #171717;
  font-weight: 700;
}

body.index ul li {
  margin-bottom: 10px;
}

body.page section h1 {
  color: #171717;
  text-transform: uppercase;
  margin: 40px 0 10px;
  font-weight: 400;
}

body.projects section {
  border-bottom: 1px solid #000;
  margin-top: 40px;
  padding-bottom: 40px;
}

body.projects section ol {
  list-style-type: decimal;
  list-style-position: inside;
}

body.projects section ol li {
  margin-bottom: 5px;
}

body.projects section ul.topics {
  -webkit-columns: 300px 2;
  -moz-columns: 300px 2;
  columns: 300px 2;
  margin-bottom: 25px;
  list-style: none;
}

body.projects section h1 {
  color: #171717;
  margin-bottom: 10px;
  font-size: 22px;
}

body.projects section h2 {
  margin-top: 0;
  font-size: 16px;
}

body.projects section h3 {
  text-transform: uppercase;
  margin: 30px 0 10px;
  font-weight: normal;
}

body.projects section:first-child {
  margin-top: 0;
}

body.schedule main section > ol {
  max-width: 800px;
  margin-left: 0;
  font-size: 16px;
}

body.schedule main section > ol > li:first-child h1 {
  border: none;
  padding-top: 0;
}

body.schedule main section > ol h2 {
  text-transform: none;
  margin: 0 0 10px;
  font-weight: bold;
}

body.schedule main section > ol h1 {
  text-transform: uppercase;
  border-top: 1px solid #000;
  margin-bottom: 10px;
  padding-top: 24px;
  font-weight: normal;
}

body.schedule main section > ol li > ol {
  grid-template-columns: 1fr 1fr;
  padding-bottom: 40px;
  display: grid;
}

@media only screen and (max-width: 470px) {
  body.schedule main section > ol li > ol {
    display: block;
  }
}

body.schedule main section > ol li > ol > li {
  padding-right: 10px;
}

body.schedule main section > ol li > ol > li li {
  font-size: 14px;
  display: block;
}

blockquote p {
  margin-top: 10px;
  font-size: 12px;
}

.projects img {
  max-width: 100%;
}

.projects ul {
  list-style: disc;
}

.projects ol li {
  list-style-position: outside;
}

.projects em {
  color: #fff;
  background: #000;
  border: 1px solid #171717;
  padding: 0 2px;
  text-decoration: none;
}

.holiday {
  text-decoration: line-through;
}

/*# sourceMappingURL=main.css.map */
